const Home = function() {

  const login = () => {
    window.location.href='/login'
  };


  const register = () => {
    window.location.href='/register'
  };

  return (
    <div>
      <button onClick={login}>Login User</button><br/><br/>
      <button onClick={register}>Register User</button><br/><br/>
    </div>
  );
};

export default Home;
